export default {
	data() {
		return {
			isLoaing:true,
			orderid: null,
			orderDetail: null,
			modelName: '',
			modelTxt: '',
			type:null
		}
	},
	onLoad(options) {
		// console.log(options)
		this.orderid = options.orderid
		this.getOrderDetail()
	},
	methods: {
		toReturnGoods() {
			uni.navigateTo({
				url: "../returnGoods/returnGoods"
			})
		},
		showModel(str) {
			this.modelTxt = str
			this.modelName = 'showModel'
		},
		getOrderDetail() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/scoreDetail",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					order_id: this.orderid
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					this.isLoaing = false
					this.orderDetail = res.data.data
					this.orderDetail.pgoods_add_time = this.handleTime(this.orderDetail.pgoods_add_time)
					uni.setStorageSync('orderDetail', this.orderDetail)
				}
			})
		},
		//处理下单时间
		handleTime(time) {
			// console.log(time)
			let date = new Date(time * 1000)
			let Y = date.getFullYear().toString()
			let M = (date.getMonth() + 1).toString().padStart(2, '0')
			let D = date.getDate().toString().padStart(2, '0')
			let h = date.getHours().toString().padStart(2, '0')
			let m = date.getMinutes().toString().padStart(2, '0')
			let s = date.getSeconds().toString().padStart(2, '0')
			let newTime = Y + '-' + M + '-' + D + ' ' + h + ':' + m + ':' + s
			return newTime
		},
		//跳转选择售后类型
		toAftersale() {
			uni.redirectTo({
				url: "../chooseAftersaleType/chooseAftersaleType"
			})
		},
		closeModel() {
			this.modelName = ''
		},
		//点击弹出框里的确认
		sure() {
			switch (this.modelTxt) {
				case '删除订单':
					this.sureDel()
					break;
				case '取消订单':
					this.cancleOrder()
					break;
				case '收货':
					this.sureGet()
					break;
				default:
					break;
			}
			this.closeModel()
		},
		//取消订单
		cancleOrder() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/orderCancel",
				data: {
					id: this.orderid,
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.msg == '操作成功') {
						var pages = getCurrentPages();
						var prevPage = pages[pages.length - 2]; //上一个页面
						prevPage.setData({
							"list": []
						}) //更新上一个页面列表数据为空
						uni.navigateBack({
							delta: 1,
							success: function(e) {
								var page = getCurrentPages().pop();
								if (page == undefined || page == null) return;
								page.onLoad();
							}
						}) //返回上一个页面更新数据
						// uni.showToast({
						// 	title: '订单已取消',
						// 	icon: 'success',
						// 	success: () => {
						// 		setTimeout(function(){
						// 			uni.navigateBack({
						// 				delta:1
						// 			})
						// 		},1500)
						// 	}
						// })
					}
				}
			})
		},
		//订单付款
		orderPay() {
			uni.request({
				url: this.websiteUrl + '/api/Order/pay',
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					order_id: this.orderid
				},
				header: this.header,
				method: "POST",
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: res.data.data.timeStamp,
							nonceStr: res.data.data.nonceStr,
							package: res.data.data.package,
							signType: res.data.data.signType,
							paySign: res.data.data.paySign,
							success: function(res) {
								console.log('success:' + JSON.stringify(res));
								uni.reLaunch({
									url: "../paySuccess/paySuccess?type=1"
								})
							},
							fail: function(err) {
								console.log('fail:' + JSON.stringify(err));
							}
						});
					}

				}
			})
		},
		//删除订单
		sureDel() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/orderDelete",
				data: {
					order_id: this.order_id,
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.msg == '操作成功') {
						uni.showToast({
							title: '订单已删除',
							icon: 'success',
							success: () => {
								setTimeout(function() {
									uni.navigateBack({
										delta: 1
									})
								}, 1500)
							}
						})
					}
				}
			})
		},
		//提醒发货
		remindSend() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/remindSend",
				data: {
					token: uni.getStorageSync('token'),
					order_id: this.order_id,
					shop_id: uni.getStorageSync('shop_id')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.msg == '操作成功') {
						this.showToast('已提醒商家尽快发货')
					}
				}
			})
		},
		//确认收货
		sureGet() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Order/confirmGet",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					order_id: this.orderid
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.msg == '操作成功') {
						uni.showToast({
							title: '已确认收货',
							icon: 'success',
							success: () => {
								setTimeout(function() {
									uni.navigateBack({
										delta: 1
									})
								}, 1000)
							}
						})
					}
				}
			})
		}
	}
}
